import { loadWAFData } from "../../utils/helpers";

export const initializeDroppableArea = (droppableContainer, handleDrop) => {
  window.require(["DS/DataDragAndDrop/DataDragAndDrop"], (DataDragAndDrop) => {
    DataDragAndDrop.droppable(droppableContainer, {
      drop: (data) => {
        // console.log("[DragAndDrop] Drop event:", data);
        const parsedData = JSON.parse(data);
        handleDrop(parsedData.data.items);
      },
      enter: () => {
        droppableContainer.classList.add("drag-over");
      },
      leave: () => {
        droppableContainer.classList.remove("drag-over");
      },
    });
  });
};

export const fetchCsrfTokenAndDependencies = async ({
  dataItems,
  setCsrfHeaders,
  onSuccess,
  onError,
}) => {
  try {
    const WAFData = await loadWAFData();

    const csrfURL =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/application/CSRF?tenant=OI000186152";

    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(csrfURL, {
        method: "GET",
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    const csrfToken = response.csrf.name;
    const csrfValue = response.csrf.value;
    const securityContextHeader = "SecurityContext";
    const securityContextValue =
      "ctx%3A%3AVPLMProjectLeader.BU-0000001.Rosemount%20Flow";

    const headers = {
      [csrfToken]: csrfValue,
      [securityContextHeader]: securityContextValue,
    };

    if (setCsrfHeaders) setCsrfHeaders(headers);

    if (onSuccess) onSuccess(headers, dataItems); // Pass both headers and dataItems
  } catch (error) {
    console.error("[CSRF] Failed to fetch token:", error);

    if (onError) onError(error);
  }
};

export const fetchObjectDetails = async ({ dataItems, headers }) => {
  try {
    if (!dataItems || dataItems.length === 0) {
      console.error("[Object Details] No items to fetch.");
      return;
    }
    const objectID = dataItems[0]?.objectId;
    if (!objectID) {
      console.error(
        "[Object Details] Missing or invalid object ID:",
        dataItems
      );
      return;
    }
    const objectDetailsURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/${objectID}?$mask=dsmveng:EngItemMask.Details`;
    const WAFData = await loadWAFData();
    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(objectDetailsURL, {
        method: "GET",
        headers,
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });
    // Return an object indicating success and the fetched data
    return {
      success: true,
      data: {
        draggedData: {
          member: response.member,
          totalItems: response.totalItems,
          nlsLabel: response.nlsLabel,
        },
      },
    };
  } catch (error) {
    console.error("[Object Details] Failed to fetch data:", error);
    return { success: false, error };
  }
};

export const fetchCardOwnerDetails = async ({ dataItems, headers }) => {
  try {
    if (!dataItems || dataItems.length === 0) {
      console.error("[Object Details] No items to fetch.");
      return;
    }
    const objectID = dataItems[0]?.objectId;

    if (!objectID) {
      console.error(
        "[Object Details] Missing or invalid object ID:",
        dataItems
      );
      return;
    }

    const cardOwnerDetailsURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/documents/${objectID}?xrequestedwith=xmlhttprequest`;
    const WAFData = await loadWAFData();
    const response = await new Promise((resolve, reject) => {
      WAFData.authenticatedRequest(cardOwnerDetailsURL, {
        method: "GET",
        headers,
        type: "json",
        onComplete: resolve,
        onFailure: reject,
      });
    });

    // Return an object indicating success and the fetched data
    return {
      success: true,
      data: {
        ownerData: {
          cardownerdata: response.data,
        },
      },
    };
  } catch (error) {
    console.error("[Card Owner Details] Failed to fetch data:", error);
    return { success: false, error };
  }
};
