import { useCallback, useState } from "react";
import { useDispatch } from "react-redux";
import { setDroppedObjectData } from "../store/droppedObjectSlice";
import { setIsDropped as setIsDroppedAction } from "../store/droppedObjectSlice";
// Custom hook
import useRevisions from "./useRevisions";
// Reusable services
import {
  initializeDroppableArea as initDroppable,
  fetchCsrfTokenAndDependencies as fetchCsrfService,
  fetchObjectDetails as fetchObjectDetailsService,
  fetchCardOwnerDetails as fetchCardOwnerDetailsService,
} from "../services/api/droppableService";
import useToast from "../hooks/useToast";
import {
  MSG_FETCH_CSRF_HEADERS_FAILED,
  MSG_FETCH_OBJECT_DETAILS_FAILED,
  MSG_UNEXPECTED_ERROR,
} from "../utils/toastMessages";

const useDroppableArea = () => {
  const { showErrorToast } = useToast();
  const { fetchRevisionsAndParents } = useRevisions();
  const [csrfHeaders, setCsrfHeaders] = useState(null);
  const [loading, setLoading] = useState(false);
  const dispatch = useDispatch();

  const fetchObjectDetails = useCallback(
    async (dataItems, headers) => {
      try {
        // Call fetchObjectDetails in droppableService.js
        const objectDetailsResult = await fetchObjectDetailsService({
          dataItems,
          headers,
        });

        const cardownerResult = await fetchCardOwnerDetailsService({
          dataItems,
          headers,
        });

        if (objectDetailsResult.success && cardownerResult.success) {
          // Merge the data from both services
          const combineData = {
            draggedData: objectDetailsResult.data.draggedData,
            ownerData: cardownerResult.data.ownerData,
          };

          dispatch(
            setDroppedObjectData({
              ...combineData,
            })
          );

          dispatch(setIsDroppedAction(true));

          // call revisions and parents after successfully fetching object details
          await fetchRevisionsAndParents(
            dataItems[0]?.objectId,
            dataItems[0]?.type,
            headers
          );
        } else {
          showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
        }
      } catch (error) {
        console.error("[FetchObjectDetails] Error fetching details:", error);
        showErrorToast(MSG_FETCH_OBJECT_DETAILS_FAILED);
      }
    },
    [dispatch, fetchRevisionsAndParents]
  );

  const handleDrop = useCallback(
    async (dataItems) => {
      try {
        setLoading(true); // Start loading state
        if (!csrfHeaders) {
          await fetchCsrfService({
            dataItems,
            setCsrfHeaders,
            onSuccess: async (headers, dataItems) => {
              await fetchObjectDetails(dataItems, headers);
              setLoading(false);
            },
            onError: (error) => {
              console.error("[Drop] CSRF Error:", error);
              setLoading(false);
              showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
            },
          });
        } else {
          await fetchObjectDetails(dataItems, csrfHeaders);
          setLoading(false);
        }
      } catch (error) {
        console.error("[Drop] Error in handleDrop:", error);
        setLoading(false);
        showErrorToast(MSG_UNEXPECTED_ERROR);
      }
    },
    [csrfHeaders, fetchObjectDetails, setCsrfHeaders]
  );
  // Initialize droppable area
  const initializeDroppableArea = useCallback(() => {
    dispatch(setIsDroppedAction(false));
    const interval = setInterval(() => {
      const droppableContainer = document.querySelector(".droppable-container");
      if (droppableContainer) {
        clearInterval(interval);
        initDroppable(droppableContainer, handleDrop);
      }
    }, 100); // Check every 100ms

    return () => clearInterval(interval); // Cleanup interval on unmount
  }, [handleDrop, dispatch]);

  return {
    initializeDroppableArea,
    loading,
    handleDrop,
  };
};

export default useDroppableArea;
