import { useDispatch } from "react-redux";
import {
  setDroppedObjectData,
  setParentDetailsLoading,
} from "../store/droppedObjectSlice";
import { loadWAFData } from "../utils/helpers";

const useRevisions = () => {
  const dispatch = useDispatch();

  const fetchRevisionsAndParents = async (objectId, type, headers) => {
    if (!headers) {
      console.error("[Fetch Revisions] Missing CSRF headers.");
      return;
    }

    const revisionURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/version/getGraph`;
    const parentURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/locate`;

    try {
      dispatch(setParentDetailsLoading(true));

      const WAFData = await loadWAFData();

      // Fetch the revisions
      const response = await new Promise((resolve, reject) => {
        WAFData.authenticatedRequest(revisionURL, {
          method: "POST",
          headers: {
            ...headers,
            "Content-Type": "application/json",
          },
          data: JSON.stringify({
            data: [
              {
                id: objectId,
                identifier: objectId,
                type: type,
                relativePath: `/resources/v1/modeler/dseng/dseng:EngItem/${objectId}`,
              },
            ],
          }),
          timeout: 150000,
          type: "json",
          onComplete: resolve,
          onFailure: reject,
        });
      });

      // Ensure response is parsed as JSON if needed
      const revisionResponse =
        typeof response === "string" ? JSON.parse(response) : response;

      const versions = revisionResponse?.results?.[0]?.versions;

      dispatch(
        setDroppedObjectData({
          versions,
        })
      );
      // Fetch parent details for all revisions
      const parentDetails = await Promise.all(
        versions.map((revision) =>
          fetchParentInformation(revision, parentURL, WAFData, headers)
        )
      );
      const flattenedParents = parentDetails.flat();

      dispatch(
        setDroppedObjectData({
          parentDetails: flattenedParents,
        })
      );
    } catch (error) {
      console.error("[Fetch Revisions] Error occurred:", error);
      dispatch(setParentDetailsLoading(false));
    } finally {
      dispatch(setParentDetailsLoading(false)); // End loading at the top level
    }
  };

  const fetchParentInformation = async (
    revision,
    parentURL,
    WAFData,
    headers
  ) => {
    const requestBody = {
      referencedObjects: [
        {
          id: revision.id,
          identifier: revision.id,
          type: revision.type,
          relativePath: revision.relativePath,
          maturityState: revision.maturityState,
          revision: revision.revision,
          source: revision.source,
        },
      ],
    };

    try {
      // Step 1: Fetch parent relationships
      const parentResponse = await new Promise((resolve, reject) => {
        WAFData.authenticatedRequest(parentURL, {
          method: "POST",
          headers: {
            ...headers,
            "Content-Type": "application/json",
          },
          data: JSON.stringify(requestBody),
          timeout: 150000,
          type: "json",
          onComplete: resolve,
          onFailure: reject,
        });
      });

      const parents =
        parentResponse.member?.[0]?.["dseng:EngInstance"]?.member || [];

      // Deduplicate based on parentObject.identifier
      const uniqueParents = parents.reduce(
        (acc, parent) => {
          const identifier = parent?.parentObject?.identifier;
          if (identifier && !acc.map.has(identifier)) {
            acc.map.set(identifier, true);
            acc.unique.push(parent);
          }
          return acc;
        },
        { map: new Map(), unique: [] }
      ).unique;

      if (!uniqueParents.length) {
        console.warn("[Fetch Parents] No valid parents found.");
        return [];
      }

      // Step 3: Fetch metadata for each valid parent
      const metadataPromises = uniqueParents.map(async (parent) => {
        const metadataURL = `${parent.parentObject.source}${parent.parentObject.relativePath}?$mask=dsmveng:EngItemMask.Details`;
        try {
          const response = await fetch(metadataURL, {
            method: "GET",
            headers: {
              ...headers,
              "Content-Type": "application/json",
              "x-requested-with": "xmlhttprequest",
            },
            credentials: "include",
          });

          if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
          }

          const metadataResponse = await response.json();
          const metadata = metadataResponse.member?.[0];

          return {
            childId: revision.id,
            connectedChildRevision: revision.revision,
            parentId: parent.parentObject.identifier,
            parentTitle: metadata?.title || "Unknown Title",
            parentMetadata: metadata,
          };
        } catch (error) {
          console.error("[Fetch Parent Metadata] Error:", error);
          return null;
        }
      });

      const detailedParents = (await Promise.all(metadataPromises)).filter(
        (parent) => parent !== null
      );

      return detailedParents;
    } catch (error) {
      console.error("[Fetch Parents] Error occurred:", error);
      return [];
    }
  };

  return { fetchRevisionsAndParents };
};

export default useRevisions;
